/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-txp-slider {
	background-color: #969696;
	color: #323232;
}

.txp-dynamic-align-center {
	text-align: center;
}
.txp-dynamic-align-left {
	text-align: left;
}
.txp-dynamic-align-right {
	text-align: right;
}

.swiper-slide {

	aspect-ratio: 16/9;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;

	h2.txp-post-title {
		font-size: 1.6em;
		color: #FFF;
		
		a.txp-post-link {
			&:hover {
				text-decoration: none;;
			}
			&:visited {
				text-decoration: none;
			}			
			.txp-publish-date {
				color: #FFF;
			}
		}
	}
}
